___CSS_LOADER_EXPORT___.push([module.id, `.run-monitor {
  border: 1px solid var(--line);
  background: #fff;

  padding: 14px;
  margin-top: 12px; /* nav    */
  height: 360px; /*   ( 320~400 ) */
  display: flex;
  flex-direction: column;
}

.rm-title {
  font-size: 13px;
  color: var(--ink-500);
  margin-bottom: 8px;
  letter-spacing: 0.3px;
}

.rm-row {
  font-size: 15px;
  color: var(--ink-700);
  line-height: 1.28;
}
.rm-row .rm-row {
  margin-top: 4px;
}
.mono {
  font-family: Consolas, 'Courier New', ui-monospace, monospace;
  letter-spacing: 0.2px;
}

.chips {
  display: flex;
  gap: 8px;
  align-items: center;
}
.chip {
  display: inline-block;
  border: 1px solid var(--line);
  background: #fff;
  font-weight: 700;
  line-height: 1;
  font-size: 13px;
  padding: 4px 10px;
}

#rm-line1 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/*  AUTO TEST (  ) */
.rm-subtitle {
  margin-top: 10px;
  font-size: 12px;
  color: var(--ink-500);
  letter-spacing: 0.3px;
  text-transform: uppercase;
  border-top: 1px solid var(--line);
  padding-top: 8px;
}
.rm-scroll {
  margin-top: 6px;
  overflow: auto; /*      */
}
.rm-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 6px;
}
.rm-item {
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
  gap: 8px;
  border: 1px solid var(--line);
  padding: 6px 8px;
  font-size: 13px;
  background: #fff;
}
.rm-item .task {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.rm-item .meta {
  font-variant-numeric: tabular-nums;
  font-size: 12px;
  color: var(--ink-600);
}
