___CSS_LOADER_EXPORT___.push([module.id, `.toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
}
#view-title {
  font-size: 28px;
  margin: 0;
}
.toolbar-right {
  gap: 16px;
}

.btn {
  border: 1px solid var(--line);
  font-weight: 700;
  font-size: 16px;
  background: #fff;
  cursor: pointer;
  height: 46px;
  padding: 0 20px;
}

/*  E-STOP + () */
.btn.estop {
  min-width: 120px;
}

/* READY:  +      */
.btn.estop[data-state='ready'] {
  background: #fff;
  color: var(--brand-red);
  border-color: var(--brand-red);
}

/* WARNING:    */
.btn.estop[data-state='warning'] {
  background: #fff7ed;
  color: var(--state-amber-900);
  border-color: var(--state-amber);
}

/* EMERGENCY:   (   ) */
.btn.estop[data-state='emergency'] {
  background: var(--brand-red);
  color: #fff;
  border-color: var(--brand-red);
}

/* E-STOPPED():    ,   RESET  */
.btn.estop[data-state='latched'] {
  background: var(--state-red-dark);
  color: #fff;
  border-color: var(--state-red-dark);
}

.btn.ghost {
  background: #fff;
  color: var(--ink-700);
}
