___CSS_LOADER_EXPORT___.push([module.id, `/*   100% */
.app {
  display: grid;
  grid-template-columns: 260px 1fr; /* Run Monitor  */
  width: 100%;
  height: 100%;
}

/* :   ,     */
.sidebar {
  background: var(--white);
  border-right: 1px solid var(--line);
  display: flex;
  flex-direction: column;
  gap: 12px; /* 1412 */
  padding: 14px; /* 1614 */
  padding-bottom: 12px; /* footer    */
}

.brand-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 6px 10px;
  border-bottom: 1px solid var(--line);
}
.brand {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.brand img {
  width: 140px;
  height: auto;
  display: block;
  object-fit: contain;
}

/*    */
.main {
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.content {
  display: grid;
  gap: 20px;
}
